.register{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    justify-content: center; 
    background: url("https://images.pexels.com/photos/317355/pexels-photo-317355.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    background-size: cover;
    align-items: center;
}

.registertitle{
    font-size: 50px;
    color: rgb(236, 40, 26);
}
.registerform{
    margin-top: 20px;
    display: flex;
    color: rgb(3, 41, 16);  
    padding: 30px;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.1);
  box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
  border-top: 2px solid rgba(255, 255, 255, 0.1);
  border-left: 2px solid rgba(255, 255, 255, 0.1);

}

.registerform > label{
    margin: 10px 0;

}

.registerinput{
    padding: 10px;
    background-color: #fff;
    border: none;

}
.registerinput:focus{
    outline: none;
}

.Registerbtn{
    margin-top: 20px;
    cursor: pointer;
    background-color: teal;
    border:none;
   color: white;
   border-radius: 10px;
   padding: 10px;
}
.Registerbtn:hover{
   color: rgb(243, 234, 234);
   background: rgb(54, 1, 36);
}
.loginbtn{
    position: absolute;
    top: 18px;
    right: 30px;
    background-color: lightcoral;
    border: none;
    cursor: pointer;
    padding: 10px;
    color: white;
    border-radius: 10px;
}