.setting{
    display: flex;
    justify-content: center;
    align-items: center;
   

}

.settingwrapper{
    background: rgba(255, 255, 255, 0.1);
  box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
  border-top: 2px solid rgba(255, 255, 255, 0.1);
  border-left: 2px solid rgba(255, 255, 255, 0.1);

    padding: 20px;
    width: 600px;

}

.settingTitle{
    display: flex;
    align-content: center;
    justify-content: space-between;

}

.settingupdateTitle{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
}

.settingdeleteTitle{
     color: red;
     font-size: 13px;
     cursor: pointer;
}
.settingform{
    display: flex;
    flex-direction: column;

}

.settingImg{
    width: 80px;
    height: 80px;
    border-radius: 20px;
    object-fit: cover;
}

.settingppIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: lightcoral;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
   margin-left: 10px;
   cursor: pointer;
}


.settingpp{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.settingform > label{
    font-size: 20px;
    margin-top: 20px;
}

.settingform > input{
    color: gray;
    margin: 10px 0;
    text-decoration: none;
    height: 30px;
    border: none;
   border-bottom: 1px solid lightgray;
}
.settingform > input:focus{
    text-decoration: none;
    outline: none;
}

.settingsubmit{
    width: 150px;
    align-self: center;
     border: none;
     border-radius: 10px;
     color: white;
     background-color: teal;
     padding: 10px;
     margin-top: 20px;
     cursor: pointer;
}