.contactme{
     position: relative;
     display: grid;
     justify-content: center;
     align-items: center;
     margin-top: 40px;

     height: 470px;
    width: 50%;
    margin-left: 350px;
     margin-bottom: 40px;
     background: rgba(255, 255, 255, 0.1);
     box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
     border-top: 2px solid rgba(255, 255, 255, 0.1);
     border-left: 2px solid rgba(255, 255, 255, 0.1);
}


.container{
    position: relative;
    display: grid;
    justify-content: center;
}

.formWord{
    color: rgb(59, 1, 21);
    font-size: 20px;
}
.input100{
    width: 600px;
    border-radius: 10px;
}
.input100:focus{
    
    text-decoration: none;
}

.msg{
    width: 600px;
    height: 100px;
    border-radius: 10px;
}

.msg:focus{
    outline: none;
}

.btn{
  width: 170px;
    margin-top: 10px;
    background-color: rgb(4, 90, 15);
    color: wheat;
}
.btn:hover{
    color: rgb(241, 234, 234);
    background-color: rgb(15, 2, 2);
}