.write{
    padding-top: 50px;

}

.writeImg{
    margin-left: 150px;
    width: 120vh;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
}

.writeform{
  position: relative;
}

.writeformg{
    margin-left: 150px;
    display: flex;
    align-items: center;


}
.writeIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: gray;
    cursor: pointer;
}

.writeinput{
  font-size: 30px;
  border: none;
  padding: 10px;
  width: 120vh;

}

.writeinput:focus{
    outline: none;
}

.writetext{
    font-size: 20px;
    height: 100vh;
}

.writesubmit{
    position: absolute;
   top: 20px;
   right: 50px;
   color: white;
   background-color: teal;
   padding: 10px;
   border: none;
   border-radius: 10px;
   cursor: pointer;
   font-size: 17px;
}

.writesubmit:hover{
    color: white;
    background: rgb(46, 0, 0);
}