.top{
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    font-family:  'Josefin Sans' sans-serif;
    margin-bottom: 20px;
    background: white;
    box-shadow: 15px 15px 15px rgba(0,0,0,0.5);
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    border-left: 1px solid rgba(255, 255, 255, 0.1);
}
.a i{
    transition: all .03s linear;
}
a:hover i{
    transform: scale(1.4) ;
}

.topleft, .topright{
  flex: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}
.topcenter{
    flex: 6;
}
.topIcon{
    font-size: 25px;
    margin-right: 10px;
    color: rgb(4, 2, 27);
    cursor: pointer;
}
.topIcon:hover{
    color: rgb(110, 109, 0);
}
.topimg{
    width: 64px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-top: 10px;
}

.toplist{
    display: flex;
    justify-content: center;
    margin: 0%;
    padding: 0%;
    list-style: none;
}
.toplistitem{
    margin-right: 15px;
    font-size: 18px;
    font-weight: 300;
    padding-left: 20px;
    cursor: pointer;
    transition: all .03s linear;

}
.toplistitem:hover{
    transform: scale(1.4) ;
}
.link{
    font-size: 20px;
    color: rgb(9, 3, 48);
    transition: all .03s linear;
}
.link:hover {
    color: rgb(250, 0, 0);
   
    text-decoration: none;
    transform: scale(1.4) ;
    
}
.topsearchIcon{
    font-size: 18px;
    color: rgb(53, 28, 28);
    cursor: pointer;
    margin-left: 20px;

}

.regis{
    margin-left: 40px;

}

.user{
    margin-left: 20px;
  
}