.BlogItem-container {
    display: flex;
    flex-direction: column;
   padding: 40px;
   margin-right: 14px;
   margin-left: 14px;
   background: rgba(255, 255, 255, 0.1);
   box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
   border-top: 1px solid rgba(255, 255, 255, 0.1);
   border-left: 1px solid rgba(255, 255, 255, 0.1);
  }
  .blogItem-cover {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 20px;
    margin-bottom: 0.5rem;
  }
  .BlogItem-container h3 {
    margin: 0.5rem 0 1rem 0;
    flex: 1;
  }
  
  .blogDesc {
    position: relative;
    max-height: 50px;
    overflow: hidden;
    padding-right: 0.6rem;
    font-size: 0.8rem;
    color: #a9a9a9;
  }
  
  .blogDesc::before {
    position: absolute;
    content: '...';
    bottom: 0;
    right: 0;
  }
  
  .BlogItem-container footer {
    display: flex;
    align-items: center;
    margin-top: 1rem;
    justify-content: space-between;
  }
  
  .blogItem-link {
    text-decoration: none;
    color: inherit;
  }
  
  .blogItem-author {
    display: flex;
    align-items: center;
  }
  
  .blogItem-author img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 0.5rem;
  }
  
  .blogItem-author p {
    font-size: 0.6rem;
    color: #a9a9a9;
    font-weight: 600;
  }
  